import React from 'react';
import CSSModules from 'react-css-modules';
import styles from './listItem.css';

function ListItem(props) {
    return(
        <li styleName="root">
            <div styleName="left">
                <p>
                    <span styleName="unit"></span>
                    <span styleName="num">11</span>
                </p>
                <p styleName="status"></p>
            </div>
            <div styleName="right">
                <p styleName="name">200011</p>
                {props.details ? (
                    <div styleName="operate">
                        <span styleName="btn" onClick={props.toConvert}></span>
                        <span styleName="link"></span>
                    </div>
                ) : (
                    <div styleName="operate">
                        <div>
                            <p styleName="text">:1000</p>
                            <p styleName="text">:3785</p>
                        </div>
                        <span styleName="btn" onClick={props.toConvert}></span>
                    </div>
                )}

            </div>
        </li>
    )
}
export default CSSModules(ListItem,styles);


