import React,{Component} from 'react';
import CSSModules from 'react-css-modules';
import styles from './promotion.css';
import ReactDocumentTitle from '../common/reactDocumentTitle/reactDocumentTitle';
import {Link} from 'react-router-dom';

class Promotion extends Component{
    render(){
        const {match}=this.props;
        return(
            <ReactDocumentTitle title=''>
                <div styleName="root">
                    <p styleName="title"></p>
                    <ul styleName="list">
                        <li styleName="list-item">
                            <p styleName="name"></p>
                            <p styleName="content">100</p>
                            <p styleName="condition">(100)</p>
                            <Link to={`${match.path}/convert`}>
                                <button styleName="btn"></button>
                            </Link>
                        </li>
                        <li styleName="list-item">
                            <p styleName="name"></p>
                            <p styleName="content">800</p>
                            <p styleName="condition">(800)</p>
                            <Link to={`${match.path}/convert`}>
                                <button styleName="btn"></button>
                            </Link>
                        </li>
                    </ul>
                </div>
            </ReactDocumentTitle>
        )
    }
}
export default CSSModules(Promotion,styles);


